.container {
    text-align: center;
    margin: 20px;
  }
  
  .game-management {
    display: flex;
    justify-content: space-between;
    margin-bottom: 30px; /* Increase spacing from the game elements */
    padding: 10px 0;
  }
  
  .game-management button {
    padding: 10px 20px;
    font-size: 18px;
    background-color: #ffeb3b;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .game-management button:hover {
    background-color: #ffd600;
  }
  
  .letter-grid {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 10px;
    margin-bottom: 20px;
  }
  
  .letter {
    width: 60px;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    background-color: lightgray;
    font-size: 24px;
    font-weight: bold;
    color: #333;
    cursor: pointer;
  }
  
  .letter.central {
    background-color: #ffeb3b;
    color: black;
  }
  
  input[type="text"] {
    padding: 10px;
    font-size: 18px;
    width: 250px;
    margin-bottom: 20px;
    border-radius: 5px;
    border: 1px solid #ccc;
  }
  
  .word-list {
    text-align: left;
    margin-top: 20px;
    max-height: 200px;
    overflow-y: auto;
  }
  
  .word-list li {
    font-size: 18px;
    color: #555;
  }
  
  .score {
    font-size: 24px;
    color: #333;
  }
  
  .controls {
    margin-top: 20px;
  }
  
  button {
    margin-right: 10px;
  }
  